@import url(https://fonts.googleapis.com/css2?family=Rampart+One&family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  color: rgb(27, 27, 27);
  max-width: 100vw;
  max-height: 100vh;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  font-family: "Poppins", sans-serif;

}

.App header {
  min-height: 10vh;
  font-family: 'Rampart One', cursive;
  font-size: 2.5rem;
  font-weight: bold;
  text-transform: uppercase;
}
form {
    position: relative;
}

form input,
form button {
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
    outline: none;
    border-radius: 3px;
    color: rgb(27, 27, 27);
}

form input {
    border-bottom: 1px solid rgb(175, 175, 175);
    margin-left: 30px;
}

form input:focus {
    outline: none;
    border-bottom: 1px solid rgb(255, 118, 68);
}

form input::-webkit-input-placeholder {
    padding-left: 20px;
}

form input:-ms-input-placeholder {
    padding-left: 20px;
}

form input::placeholder {
    padding-left: 20px;
}

form button {
    position: absolute;
    right: 400px;
    cursor: pointer;
    font-weight: bold;
    margin-left: 10px;
}

form button:hover {
    color: rgb(255, 118, 68);
    font-size: 2.6rem;
    font-weight: normal;
}
.todo-filter button {
    padding: 0.1rem 0.4rem;
    font-family: 'Rampart One', cursive;
    font-size: 1.5rem;
    font-weight: bold;
    justify-content: space-between;
    border: none;
    cursor: pointer;
}

.todo-filter button:hover,
.active {
    color: rgb(255, 118, 68);
}
.todoList-item {
    display: flex;
    justify-content: space-between;
    padding: 1rem 1rem 0rem 1rem;
    color: rgb(27, 27, 27);
    font-size: 1.5rem;
    align-items: center;
    margin-bottom: 12px;
    border-bottom: 1px solid rgb(175, 175, 175);
    border-radius: 5px;
}

.todoList-check {
    left: 40px;
    cursor: pointer;
}

.todoList-text {
    padding-left: 10px;
    padding-right: 10px;
    min-width: 300px;
    font-size: 24px;
}

.todo-edit button,
.todo-delete button {
    border: none;
    padding: 1px;
    cursor: pointer;
}

.todo-edit button:hover {
    color: seagreen;
}

.todo-delete button:hover {
    color: rgb(235, 78, 78);
}

.line-through {
    text-decoration: line-through;
    opacity: 0.4;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    cursor: pointer;
    z-index: 999;

}

.modal-container {
    position: absolute;
    top: 35%;
    left: 50%;
    -webkit-transform: translate(-50%, -25%);
            transform: translate(-50%, -25%);
    background-color: white;
    cursor: default;
    border-radius: 10px;
    padding: 2rem;
    max-width: 50ch;
    box-shadow: 0 3px 6px rgba(240, 237, 237, 0.16), 0 3px 6px rgba(243, 100, 100, 0.23);
}

form input {
    margin: 0;
}

.modal-content {
    padding: 0.2rem;
}

.edit-title {
    position: absolute;
    top: 10px;
    left: 15px;
    font-size: 1.5rem;
    font-weight: bold;
    width: 100%;
    color: rgb(37, 37, 37);
    font-weight: bold;
    text-transform: uppercase;
}

.edit-input {
    background: transparent;
    width: 90%;
    right: 20%;
    padding: 20px;
    padding-bottom: 0;
    font-size: 30px;
}

.close-btn {
    position: absolute;
    cursor: pointer;
    top: 5px;
    left: 92%;
    border: none;
    font-weight: bold;
    background: transparent;
    color: red;
}

.check-btn {
    position: absolute;
    cursor: pointer;
    left: 92%;
    outline: none;
    background: transparent;
    bottom: 0;
}

.check-btn {
    color: orangered;
}

.check-btn:active,
.check-btn:hover {
    color: green;
}

.close-btn:hover {
    color: green;
}
